#lifeof {
    
    font: bold 4rem Helvetica;
   
    text-align: center;
}


.lefty{
    float: right;
}
 

.whichone{
    font: bold 2em Helvetica;
    list-style: none;
    margin-top: -300px;
}

#whichone li{
    display: inline-block;
}

#whichone #righter{
    margin-right: 5%;
}

.maingrid{
    min-height: 900px;
    vertical-align: top;
}

.One{
    display: inline-block;
    position: relative;
    bottom: 435px;
    right: 90px;
    width: 400px;
    height: 275px;

}

.Zero{
    display: block;
    position: relative;
    bottom: 250px;
    left: 625px;
    height: 275px;
    width: 350px;
}


@media screen and (max-width:1500px){
    #lifeof {
        font: bold 3rem Helvetica;
        text-align: center;
    }
    .One{
        bottom: 325px;
        right: 70px;
        width: 300px;
        height: 200px;
    }
    .whichone{
        font-size: 1.5rem;
        margin-top: -250px;
    }
    .maingrid{
        min-height: 700px;
        max-height: 700px;
    }

    .Zero{
        bottom: 195px;
        left: 375px;
        height: 200px;
        width: 225px;
    }
    
}

@media screen and (max-width:1000px){
    #lifeof {
    
        font: bold 2rem Helvetica;
        text-align: center;
    }
    
    .One{
        bottom: 210px;
        right: 45px;
        width: 195px;
        height: 135px;
    }

    .whichone{
        font-size: 1rem;
        margin-top: -125px;
      
    }

    .maingrid{
        min-height: 450px;
        max-height: 450px;
    }
}

@media screen and (max-width:600px){
    #lifeof {
    
        font: bold 1.5rem Helvetica;

        text-align: center;
    }

    .One{
        bottom: 165px;
        right: 35px;
        width: 150px;
        height: 105px;
    }


    .whichone{
        font-size: 0.75rem;
        margin-top: -105px;
    }

    .maingrid{
        min-height: 300px;
        max-height: 300px;
    }
}




